* {
  font-family: 'Roboto', sans-serif;
}

body {
  margin    : 0;
  background: #f9f9f9 !important;
  color     : #565656 !important;
}

.fw100 {
  font-weight: 100;
}

.fw300 {
  font-weight: 300;
}

.fw400 {
  font-weight: 400;
}

.fw500 {
  font-weight: 500;
}

.fw700 {
  font-weight: 700;
}

.fw900 {
  font-weight: 900;
}

.App>.pageTitle {
  padding      : 10px;
  background   : #f9f9f9;
  border-bottom: 1px solid #eee;
  margin-bottom: 20px;
}
.App>.pageTitle h1,h2,h3,h4,h5,h6{margin: 0!important;}

.btn-xs{
  padding: 0.175rem 0.45rem!important;
  font-size: 12px!important;
}